import { useEffect, useState } from "react";
import { Link } from "react-router-dom";

const HairProblemSection = () => {
  const [isScrolling, setIsScrolling] = useState(false);

  useEffect(() => {
    let timer;

    const handleScroll = () => {
      setIsScrolling(true);

      // Clear the timer if there's already one
      clearTimeout(timer);

      // Set a timer to stop scrolling after 150ms
      timer = setTimeout(() => {
        setIsScrolling(false);
      }, 1000000);
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
      clearTimeout(timer);
    };
  }, []);

  const [activeSection, setActiveSection] = useState("Dandruff");

  const handleLinkClick = (section) => {
    setActiveSection(section);
  };

  return (
    <div>
      <section className="service-details-page rel z-1 pt-40 rpt-35 pb-50 rpb-100">
        <div className="container">
          <div className="row">
            <div className="col-xl-9 mt-65">
              <div className="service-details-content">

              {activeSection === 'Dandruff' && (
                <div id="dandruff">
                  <h3>Hair Problems And Their Treatments</h3>
                  <div>
                    <div className="image pt-5 wow fadeInUp delay-0-2s">
                      <img
                        src="/assets/img/service-details/Dandruff-1.webp"
                        alt="Dandruff"
                      />
                    </div>
                    <h4>1. Dandruff</h4>
                    <p>
                      Dandruff causes the scalp to produce white flakes. It is a
                      harmless, non-inflammatory skin condition that affects the
                      scalp and can lead to hair loss.
                    </p>
                    <h6>Reasons for dandruff</h6>

                    <ul className="list-style-two pt-10 pb-20">
                      <li>You have dry skin.</li>
                      <p>
                        Dry skin is one of the most common causes of dandruff.
                        And unfortunately, if this is the root of your dandruff,
                        you might see it get worse this time of year. “If dry
                        skin is causing your dandruff, it’s easier to tell as
                        other places of your body are most likely dry as well,”
                        Dr. Kwon says. “And during cold months, skin becomes
                        drier – which is why people tend to notice more dandruff
                        at this time.”
                      </p>
                      <p>
                        <h6>The remedy:</h6> You know the drill: moisturize.
                        There are many shampoos (both medicated and not) that
                        replenish moisture into your scalp and can alleviate —
                        or even completely get rid of — dandruff.
                      </p>

                      <li>You aren’t shampooing enough.</li>
                      <p>
                        While it sounds unhygienic, not shampooing enough could
                        be the cause of your dandruff.“When you don’t shampoo
                        enough, oil accumulates and builds up on the skin and
                        can cause dandruff,” Dr. Kwon explains.Before you freak
                        out, this doesn’t necessarily mean you don’t clean
                        yourself enough.
                      </p>
                      <p>
                        <h6>The remedy:</h6> While the fix could be as simple as
                        shampooing more frequently, you might need to take it
                        one step further. Using a medicated over-the-counter
                        shampoo a few times a week can help prevent any build
                        up. Before shampooing give a gentle scalp massage with
                        our best ayurvedic hair oil.
                      </p>

                      <li>You have a medical condition.</li>
                      <p>
                        Seborrheic dermatitis is a chronic medical condition
                        that not only affects the scalp but also other areas of
                        the body where oil glands are present. This condition is
                        characterized by dandruff as well as redness of the
                        scalp. It looks similar to other common skin diseases
                        like psoriasis, eczema or even an allergic reaction.
                      </p>
                      <p>
                        <h6>The remedy:</h6> If you can relate to these
                        symptoms, talk with a doctor. Some tar-based shampoos
                        work well to stop skin cells from overturning too
                        quickly. Other medications and following a good skin
                        care routine can also help. One thing to note: Tar-based
                        shampoos may not be a great option for people with
                        lighter-colored hair, as this type of shampoo can
                        discolor hair, Dr. Kwon says.
                      </p>
                      <p>
                        You have a yeast overgrowth. Malassezia is a fungus that
                        lives on the scalp and skin of all people. For some, a
                        sensitivity develops which can result in dandruff.
                      </p>
                    </ul>

                    <h4>Home Remedies For Dandruff:</h4>
                    <h6>A. Hair Mask:</h6>
                    <ul className="list-style-one pt-10 pb-20">
                      <li>Apply lemon juice for 5 min</li>
                      <li>Yogurt + Papaya</li>
                    </ul>
                    <h6>B. Oil Massage:</h6>
                    <p>
                      Dry scalp can be a major contributor to dandruff. A
                      regular head massage with herbal oils can be a natural and
                      effective way to combat it. These oils help moisturize the
                      scalp, preventing dryness and flaking.
                    </p>

                    <h4>
                      Here are some popular choices for your scalp massage oil:
                    </h4>

                    <ul className="list-style-one pt-10 pb-20">
                      <li>
                        Coconut Oil: Rich in moisturizing fatty acids, coconut
                        oil deeply nourishes the scalp.
                      </li>
                      <li>
                        Almond Oil:Gentle and lightweight, almond oil soothes
                        irritation and promotes a healthy scalp.
                      </li>
                      <li>
                        Tea Tree Oil: Known for its antifungal properties, tea
                        tree oil can help combat dandruff-causing fungus. (Note:
                        Use diluted tea tree oil, as it can be strong.)
                      </li>
                      <li>
                        Camphor Oil: This invigorating oil stimulates
                        circulation and may help relieve itching. (Note: Use
                        camphor oil sparingly, as it can be irritating.)
                      </li>
                      <li>
                        Neem Oil: With natural antibacterial and antifungal
                        properties, neem oil can be a powerful weapon against
                        dandruff. (Note: Neem oil has a strong odor. Consider
                        mixing it with a carrier oil like coconut or almond
                        oil.)
                      </li>
                    </ul>

                    <p>
                      Vedarma Ayurvedic Hair Oil combines the power of nature’s
                      finest ingredients to combat dryness, dandruff, and
                      irritation.
                    </p>

                    <h4>
                      For best results use HAIR ROPAN hair oil for hair growth:
                    </h4>
                    <ul className="list-style-one pt-10 pb-20">
                      <li>
                        Warm the oil slightly before applying it to your scalp.
                      </li>
                      <li>
                        Gently massage the oil into your scalp for a few
                        minutes, focusing on areas prone to dandruff.
                      </li>
                      <li>
                        Leave the oil on your hair for at least 30 minutes
                        before shampooing.
                      </li>
                      <li>
                        Repeat this treatment 2-3 times a week for optimal
                        results.
                      </li>
                    </ul>
                  </div>
                </div>)}

                {activeSection === 'Split Ends' && (
                <div id="split-ends">
                  <div className="image pt-5 wow fadeInUp delay-0-2s">
                    <img
                      src="/assets/img/service-details/Split-ends.jpg"
                      alt="Split-ends"
                    />
                  </div>

                  <h4>2. Split ends</h4>
                  <p>
                    Division of hair shafts from the end refers to split ends.
                  </p>

                  <h5>Reasons for split ends</h5>
                  <ul className="list-style-one pt-10 pb-20">
                    <li>
                      Rubbing shampoo throughout your hair during the washing
                      process.
                    </li>
                    <li>Not using conditioners.</li>
                    <li>
                      Allowing your hair to come into contact with harsh
                      chemicals such as the chemicals in swimming pools.
                    </li>
                    <li>Using a towel to rub your hair dry.</li>
                    <li>
                      Brushing your hair after a shower while it’s still wet.
                    </li>
                    <li>
                      Using blow dryers, hot combs, and/or curling irons
                      frequently and on high settings.
                    </li>
                  </ul>

                  <h5>Home remedy for split ends</h5>
                  <p>
                    Smooth batter of ripe papaya and yogurt apply it for 30-45
                    min.
                  </p>
                </div>)}

                {activeSection === 'Greasy Hair' && (
                <div id="greasy-hair">
                  <div className="image pt-5 wow fadeInUp delay-0-2s">
                    <img
                      src="/assets/img/service-details/Oily-scalp.webp"
                      alt="cause-of-diabetes"
                    />
                  </div>
                  <h4>3. Greasy Hair (Oily scalp)</h4>
                  <p>
                    The excessive production of sebum (natural oil) in the scalp
                    makes hair greasy
                  </p>

                  <h5>Causes of oily scalp</h5>
                  <p>
                    Here are some of the most common causes of an oily scalp.
                  </p>

                  <ul className="list-style-one pt-10 pb-20">
                    <li>
                      Seborrheic dermatitis: Seborrheic dermatitis is a
                      condition that irritates and inflames your skin. Pink,
                      scaly areas of skin develop in areas with a lot of oil
                      glands, including the scalp. It’s also known as dandruff.
                      It can result from an allergic reaction or be a symptom of
                      an autoimmune condition. It’s not a serious condition.
                      Home treatments can often treat or manage it.
                    </li>
                    <li>
                      Skin conditions: Skin conditions like eczema and psoriasis
                      can cause red, scaly patches to appear on your scalp. Both
                      of these conditions are disorders related to immune
                      dysfunction.
                    </li>
                    <li>
                      Forehead acne: Forehead acne develops when oil and skin
                      cell substances get trapped inside the pores. It can also
                      lead to specific acne bacteria to build up and cause acne
                      bumps. Excessive oil production is often the cause of
                      acne.
                    </li>
                    <li>
                      Genetics: Your genes may contribute to how much oil your
                      glands produce and how thick your hair is.
                    </li>
                  </ul>
                </div>)}

                {activeSection === 'Hair Loss' && (
                <div id="hair-loss">
                  <div className="image pt-5 wow fadeInUp delay-0-2s">
                    <img
                      src="/assets/img/service-details/Hair-Loss.webp"
                      alt="cause-of-diabetes"
                    />
                  </div>
                  <h4>4. Hair Loss</h4>
                  <p>
                    It is the most common hair problem that can happen to
                    anyone.
                  </p>
                  <p>
                    We are all unique, and so is our experience with hair loss.
                    The daily pressure we put on our hair, such as while
                    brushing or washing it, is one of the most common causes of
                    hair loss. Genetics is also among the potential causes of
                    hair loss. Genetic hair loss, also known as androgenetic
                    alopecia, runs in families and primarily affects the scalp.
                    Although experiencing hair loss can be upsetting, both of
                    its causes are treatable.
                  </p>

                  <h5>Symptoms of Hair Loss:</h5>

                  <ul className="list-style-one pt-10 pb-20">
                    <li>
                      Receding Hairline: The first noticeable sign is often hair
                      thinning at the temples and progressing across the scalp.
                    </li>
                    <li>
                      Bald Patches: Circular areas of hair loss, typically
                      appearing on the scalp's crown.
                    </li>
                    <li>
                      Hair Thinning: When your scalp becomes increasingly
                      visible through your hair, this indicates hair thinning.
                    </li>
                    <li>Baldness: Permanent hair loss from the scalp.</li>
                  </ul>

                  <h5>Causes of Hair Loss:</h5>

                  <ul className="list-style-one pt-10 pb-20">
                    <li>
                      Hormonal Imbalance: Fluctuations in hormones, particularly
                      during pregnancy, menopause, and menstruation in women,
                      can contribute to hair loss.
                    </li>
                    <li>
                      Aging: Certain hair loss patterns, like male-pattern
                      baldness and female-pattern baldness, are linked to aging.
                    </li>
                    <li>
                      Stress: Telogen effluvium is a temporary hair loss
                      condition triggered by stress.
                    </li>
                    <li>
                      Chemical Exposure: Harsh chemicals in some hair products,
                      such as sulfates, sodium chloride, parabens, and propylene
                      glycol, can damage hair and hinder growth.
                    </li>
                    <li>
                      Heat and Pollution: Excessive exposure to heat and
                      pollution dries out the scalp and damaged hair, leading to
                      hair loss.
                    </li>
                    <li>
                      Scalp Infection: Scalp infections can cause itchiness,
                      dryness, redness, and damage hair follicles, resulting in
                      hair loss.
                    </li>
                    <li>
                      Medical Conditions: Medical conditions like thyroid
                      disorders, cancer, and Polycystic Ovary Syndrome (PCOS)
                      may cause temporary or permanent hair loss.
                    </li>
                    <li>
                      Poor Diet: A lack of essential vitamins and minerals in
                      your diet can weaken hair roots and hinder hair growth.
                    </li>
                    <li>
                      Unhealthy Habits: Sleep deprivation, smoking, and
                      excessive alcohol consumption can affect hair health and
                      contribute to hair loss.
                    </li>
                  </ul>

                  <h5>Potential Treatment Options:</h5>

                  <ul className="list-style-one pt-10 pb-20">
                    <li>
                      Gentle Hair Care: Be gentle when combing and washing your
                      hair to minimize breakage.
                    </li>
                    <li>
                      Dietary Changes: Include a balanced diet rich in vitamins
                      and minerals for healthy hair growth.
                    </li>
                    <li>
                      Scalp Massage: Regular scalp massage with herbal oils
                      (twice a week for 30 minutes) can improve circulation and
                      promote hair growth.
                    </li>
                    <li>
                      Natural Hair Care Products: Switch to natural hair care
                      products to avoid the harmful effects of chemicals on your
                      hair.
                    </li>
                    <li>
                      Stress Management: Practice yoga or other stress-reduction
                      techniques to manage stress levels, which can benefit hair
                      health.
                    </li>
                    <li>
                      Hair Masks: Nourish your hair with natural hair masks made
                      with beneficial ingredients.
                    </li>
                    <li>
                      Sun Protection: Protect your hair from UV rays with hats
                      or leave-in conditioners with SPF.
                    </li>
                    <li>
                      Consult a Healthcare Professional: If you suspect an
                      underlying medical condition, consult a healthcare
                      professional for proper diagnosis and treatment.
                    </li>
                  </ul>
                </div>)}

                {activeSection === 'Dry Hair' && (
                <div id="dry-loss">
                  <div className="image pt-5 wow fadeInUp delay-0-2s">
                    <img
                      src="/assets/img/service-details/Dry-Hair.webp"
                      alt="cause-of-diabetes"
                    />
                  </div>
                  <h4>5. Dry Hair</h4>
                  <p>
                    Dry hair can be a seasonal problem: your hair is affected by
                    the weather – whether that means shifts in temperature or
                    the merciless summer sun. A dry atmosphere can strip the
                    hair of moisture and make it feel even dryer, more brittle,
                    and dull. Dryness can also lead to unwanted frizz, a
                    straw-like consistency or broken hair, making it extra
                    challenging to style.
                  </p>
                  <p>
                    If you have a dry scalp, you might have dry hair too –
                    particularly if your scalp is not producing enough natural
                    oils to lubricate the hair. An irritated scalp that ends up
                    shedding and peeling can also lead to dry hair.
                  </p>

                  <h5>Symptoms of Dry Hair:</h5>

                  <ul className="list-style-one pt-10 pb-20">
                    <li>
                      Dry Flakes: Small, white flakes appearing on the scalp and
                      hair, often caused by a lack of natural scalp oils.
                    </li>
                    <li>
                      Split Ends: Hair that splits and frays at the tips,
                      indicating dryness and damage.
                    </li>
                    <li>
                      Rough Texture: Hair that feels coarse and lacks
                      smoothness, potentially due to moisture loss.
                    </li>
                    <li>
                      Tangled Hair: Hair that knots easily, making combing and
                      detangling difficult due to lack of moisture and
                      manageability.
                    </li>
                    <li>
                      Hair Fall: Increased hair shedding, which can occur when
                      hair lacks essential nutrients and hydration.
                    </li>
                  </ul>

                  <h5>Causes of Dry Hair:</h5>

                  <ul className="list-style-one pt-10 pb-20">
                    <li>
                      Dosha Imbalance (Ayurvedic Perspective): An imbalance in
                      Vata and Kapha doshas is believed to affect sebum
                      production in the scalp, leading to dry and fragile hair.
                    </li>
                    <li>
                      Heat Exposure: Excessive exposure to UV rays from sunlight
                      or heat from styling tools can deplete the hair's natural
                      oils, causing dryness.
                    </li>
                    <li>
                      Poor Blood Circulation: Insufficient blood flow to the
                      scalp can deprive hair follicles of vital nutrients,
                      resulting in brittle hair.
                    </li>
                    <li>
                      Nutritional Deficiencies: A diet lacking in vitamins and
                      minerals essential for hair health can negatively impact
                      hair texture and lead to dryness and damage.
                    </li>
                    <li>
                      Over-washing: Washing your hair too frequently can strip
                      away natural oils from the scalp, leaving hair dry and
                      frizzy.
                    </li>
                    <li>
                      Hard Water: Mineral deposits in hard water can build up on
                      hair, making it dry, brittle, and difficult to manage.
                    </li>
                  </ul>

                  <h5>Potential Treatment Options:</h5>

                  <ul className="list-style-one pt-10 pb-20">
                    <li>
                      Scalp Massage with Natural Oils: Regularly massaging your
                      scalp with natural or herbal oils (like those from
                      Vidarikand, Guduchi, Brahmi, Ashwagandha, or Shatavari)
                      can help nourish hair and improve scalp health.
                    </li>
                    <li>
                      Nutrient-Rich Diet: Incorporate a balanced diet rich in
                      nuts, fruits, vegetables, and dairy products to provide
                      your hair with the nutrients it needs to thrive.
                    </li>
                    <li>
                      Natural Hair Masks: Homemade hair masks made with natural
                      ingredients like honey, fenugreek seeds, yogurt, banana,
                      or aloe vera can provide deep conditioning and improve
                      hair moisture.
                    </li>
                    <li>
                      Chemical-Free Hair Care: Choose hair care products free of
                      harsh chemicals that can dry out your scalp and damage
                      your hair.
                    </li>
                  </ul>
                </div>)}

                {activeSection === 'Premature Graying' && (
                <div id="premature-graying">
                  <div className="image pt-5 wow fadeInUp delay-0-2s">
                    <img
                      src="/assets/img/service-details/Premature-Graying.jpg"
                      alt="cause-of-diabetes"
                    />
                  </div>
                  <h4>6. Premature Graying</h4>
                  <p>
                    Premature graying of hair can be due to deficiency of
                    vitamin B, vitamin D, vitamin E or biotin, oxidative stress
                    and lifestyle stress might also serve as a reason.
                  </p>

                  <h5>Causes of Premature graying:</h5>

                  <p>
                    <b>1. Hereditary:</b> One of the most common and major
                    factors responsible for premature grey hair. The graying
                    gene grows from birth onward, when it finally starts to show
                    up physically it is hard to turn it back to the original
                    stage.
                  </p>

                  <p>
                    <b>2. Melanin production:</b> Low or No production of
                    melanin can also be a cause for premature graying of hair.
                    The gradual decrease of melanin production on the cell
                    surface and the type of melanin being reduced will also
                    decide the graying of hair.
                  </p>

                  <p>
                    <b>3. Stress:</b> Stress is something that can affect anyone
                    at any time. The kind of stress you are going through will
                    determine how your body looks, and how your hormones and
                    hair behave. Stress can take a toll on your hair growth and
                    lead to premature graying of hair.
                  </p>

                  <p>
                    <b>4. Lack of nutrition:</b> Vitamin B 12 is a necessary
                    vitamin that promotes the nervous system, health, and brain
                    function as well as the development of cardiac muscles. It
                    also aids in the creation of red blood cells, which helps
                    nutrients transport from one part of the body to another.
                    Vitamin B 12 deficiency is also called pernicious anemia,
                    where your body can’t absorb enough of this vitamin. Which
                    weakens hair cells and affects melanin production. This can
                    have an impact on your hair health and accelerate graying of
                    hair.
                  </p>

                  <p>
                    <b>5. Smoking:</b> It is well known that smoking cigarettes
                    increases the risk of lung cancer and skin diseases. The
                    long-term effect however can go beyond the heart and lungs
                    and affect the hair. Smoking constricts blood vessels which
                    can reduce the blood flow to hair follicles and cause hair
                    loss. Additionally, the toxins in cigarettes can damage the
                    hair follicles causing early onset of graying. Premature
                    graying of hair – smoking.
                  </p>

                  <p>
                    <b>6. Autoimmune disorder:</b> An autoimmune disorder can
                    also cause premature graying of hair. This is when the
                    body’s immune system attacks its cells. In the case of
                    alopecia and vitiligo, the immune system can attack hair and
                    cause loss of pigment.
                  </p>

                  <h5>How does Hair Ropan work?</h5>

                  <p>
                    Hair Ropan has all the ingredients which works on all the
                    above mentioned hair issues.gentle massage of Hair Ropan oil
                    for 30 min and leave it for 45 min before the hair washHAIR
                    ROPAN OIL MAKES THE HAIR AS WELL AS SCALP HEALTHY.
                  </p>
                </div>)}
              </div>
            </div>

            <div className="col-xl-3 col-lg-6 col-md-8">
              <div
                className={`service-sidebar ${
                  isScrolling ? "no-animation" : ""
                }`}
              >
                <div className="widget widget-menu delay-0-4s">
                  <ul>
                    <li>
                      <Link href="/">
                        {" "}
                        <a onClick={() => handleLinkClick("Dandruff")}>
                          Dandruff
                        </a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/">
                        <a onClick={() => handleLinkClick("Split Ends")}>
                          Split Ends
                        </a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/">
                        <a onClick={() => handleLinkClick("Greasy Hair")}>
                          Greasy Hair (Oily scalp)
                        </a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/">
                        <a onClick={() => handleLinkClick("Hair Loss")}>
                          Hair Loss
                        </a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/">
                        <a onClick={() => handleLinkClick("Dry Hair")}>
                          Dry Hair
                        </a>
                      </Link>
                    </li>
                    <li>
                      <Link href="/">
                        <a onClick={() => handleLinkClick("Premature Graying")}>
                          Premature Graying
                        </a>
                      </Link>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      {/* Service Details Section End */}
    </div>
  );
};

export default HairProblemSection;
